import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PublicacioPostComponent } from './publicacio-post.component';

const routes: Routes = [{
    path: '',
    children: [
        {
            path: '',
            component: PublicacioPostComponent
        }
    ]
}];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class PublicacioPostRoutingModule { }
